/*------------------------------------*\
:    Header
\*------------------------------------*/
.c-site-header {
  position: fixed;
  background-color: #f9f8f6;
  width: 100%;
  top: 0;
  left: 0;
  text-align: center;
  z-index: 888;
  padding: 50px 0;
  transform: translateY(-100%);
  transition: transform 0.3s ease-out;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  visibility: hidden; }
  .c-site-header.has-menu-open {
    visibility: visible;
    transform: translateY(0); }

/*------------------------------------*\
:    Header - Site navigation
\*------------------------------------*/
.custom-logo-link {
  width: 30vh;
  display: inline-block;
  transition: transform 0.3s; }
  .custom-logo-link:hover {
    transform: scale(1.1); }
  .custom-logo-link img {
    width: 100%;
    height: auto; }

nav.c-site-nav ul {
  position: relative;
  margin-top: 1em; }

nav.c-site-nav li {
  margin-bottom: 1em; }
  nav.c-site-nav li.is-open .c-sub-menu {
    pointer-events: auto; }
  nav.c-site-nav li.is-open .c-sub-menu__inner {
    transform: translateY(0);
    opacity: 1; }
  nav.c-site-nav li.current-menu-item a,
  nav.c-site-nav li.current-menu-parent a {
    color: #2d72a9; }
  nav.c-site-nav li.menu-item-has-children {
    position: relative; }
    nav.c-site-nav li.menu-item-has-children > a:after {
      content: "\edbf";
      font-family: 'icomoon' !important;
      font-size: 18px;
      margin-left: 0.5em;
      vertical-align: baseline; }
      @media (min-width: 0px) and (max-width: 1024px) {
        nav.c-site-nav li.menu-item-has-children > a:after {
          font-size: 15px; } }
      @media (min-width: 0px) and (max-width: 767px) {
        nav.c-site-nav li.menu-item-has-children > a:after {
          font-size: 15px; } }

nav.c-site-nav a {
  display: block;
  color: #2b4254;
  text-decoration: none;
  font-size: 26px;
  font-family: 'Montserrat', serif;
  transition: color 0.3s; }
  @media (min-width: 0px) and (max-width: 1150px) {
    nav.c-site-nav a {
      font-size: 24px; } }
  @media (min-width: 0px) and (max-width: 1080px) {
    nav.c-site-nav a {
      font-size: 20px; } }
  @media (min-width: 0px) and (max-width: 1023px) {
    nav.c-site-nav a {
      font-size: 18px; } }
  @media (min-width: 0px) and (max-width: 767px) {
    nav.c-site-nav a {
      font-size: 26px; } }
  nav.c-site-nav a:hover {
    color: #f25132; }

nav.c-site-nav .c-sub-menu {
  position: absolute;
  left: 0;
  width: 100%;
  pointer-events: none;
  transition: height 300ms ease-in-out; }

nav.c-site-nav .c-sub-menu__inner {
  padding: 20px;
  background: #f25132;
  opacity: 0;
  transform: translateY(-50px);
  transition: transform 300ms ease-in-out, opacity 300ms ease-in-out; }

nav.c-site-nav ul.sub {
  margin-left: 0;
  display: block; }
  nav.c-site-nav ul.sub a {
    color: #fff;
    font-size: 22px; }
    @media (min-width: 0px) and (max-width: 1024px) {
      nav.c-site-nav ul.sub a {
        font-size: 18px; } }
    nav.c-site-nav ul.sub a:hover {
      color: #2d72a9; }
  nav.c-site-nav ul.sub li.current-menu-item a {
    color: #2b4254; }

@media (min-width: 0px) and (max-width: 767px) {
  nav.c-site-nav li {
    transform-style: preserve-3d;
    backface-visibility: hidden;
    transform: translateX(100%);
    transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    will-change: transform; }
    nav.c-site-nav li.current-menu-item > a,
    nav.c-site-nav li.current-menu-parent > a {
      color: #2b4254; }
    nav.c-site-nav li.menu-item-has-children > a:after {
      font-size: 16px; }
    nav.c-site-nav li:nth-child(1) {
      transition-delay: 0.1s; }
    nav.c-site-nav li:nth-child(2) {
      transition-delay: 0.2s; }
    nav.c-site-nav li:nth-child(3) {
      transition-delay: 0.3s; }
    nav.c-site-nav li:nth-child(4) {
      transition-delay: 0.4s; }
    nav.c-site-nav li:nth-child(5) {
      transition-delay: 0.5s; }
    nav.c-site-nav li:nth-child(6) {
      transition-delay: 0.6s; }
    nav.c-site-nav li:nth-child(7) {
      transition-delay: 0.7s; }
    nav.c-site-nav li.is-open .c-sub-menu {
      max-height: 999px; }
      nav.c-site-nav li.is-open .c-sub-menu li {
        transform: translateX(0%); }
  nav.c-site-nav .c-sub-menu {
    position: static;
    max-height: 0;
    transition: max-height 0.4s ease; }
    nav.c-site-nav .c-sub-menu li {
      transform: translateX(100%); }
  nav.c-site-nav .c-sub-menu__inner {
    padding: 0;
    opacity: 1;
    background: transparent; }
  nav.c-site-nav ul.sub a:hover {
    color: #2d72a9; }
  nav.c-site-nav ul.sub li.current-menu-item a {
    color: #2b4254; }
  nav.c-site-nav.is-active {
    visibility: visible; }
    nav.c-site-nav.is-active ul.menu > li {
      transform: translateX(0%); } }

/*------------------------------------*\
:    Header - Hamburger mobile
\*------------------------------------*/
.c-site-nav-button {
  width: 50px;
  height: 50px;
  position: fixed;
  top: 27px;
  right: 40px;
  z-index: 999;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s 0.5s; }
  .c-site-nav-button:hover .c-site-nav-button__line {
    background-color: #2b4254; }
  .c-site-nav-button.is-active .c-site-nav-button__line {
    background-color: #f25132;
    transition: top 150ms ease-in-out, transform 150ms 150ms ease-in-out; }
    .c-site-nav-button.is-active .c-site-nav-button__line.c-site-nav-button__line--1 {
      top: 23px;
      transform: rotate(45deg); }
    .c-site-nav-button.is-active .c-site-nav-button__line.c-site-nav-button__line--2 {
      top: 23px;
      opacity: 0;
      transition: opacity 150ms ease-in-out; }
    .c-site-nav-button.is-active .c-site-nav-button__line.c-site-nav-button__line--3 {
      top: 23px;
      transform: rotate(-45deg); }
  @media (min-width: 0px) and (max-width: 1024px) {
    .c-site-nav-button {
      right: 20px;
      top: 10px; } }
  @media (min-width: 0px) and (max-width: 767px) {
    .c-site-nav-button {
      right: 0;
      top: 0; } }

.c-site-nav-button__line {
  width: 30px;
  height: 3px;
  background: #8a9ba8;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  transition: top 150ms 150ms ease-in-out, transform 150ms ease-in-out; }
  .c-site-nav-button__line.c-site-nav-button__line--1 {
    top: 14px; }
  .c-site-nav-button__line.c-site-nav-button__line--2 {
    top: 24px;
    transition: opacity 150ms 150ms ease-in-out; }
  .c-site-nav-button__line.c-site-nav-button__line--3 {
    top: 34px; }

/*------------------------------------*\
:    Header - Back
\*------------------------------------*/
.c-nav-parent {
  position: fixed;
  z-index: 99;
  top: 27px;
  right: 100px;
  font-size: 22px;
  line-height: 1;
  height: 50px;
  color: #8a9ba8;
  display: flex;
  align-items: center;
  transition: transform 0.3s; }
  .c-nav-parent:hover {
    transform: scale(1.1); }
  .c-nav-parent:hover {
    color: #2b4254;
    text-decoration: none; }
  .c-nav-parent .icon-arrow-left {
    font-size: 30px; }
  @media (min-width: 0px) and (max-width: 1024px) {
    .c-nav-parent {
      top: 10px;
      right: 80px; } }
  @media (min-width: 0px) and (max-width: 767px) {
    .c-nav-parent {
      top: 0;
      right: 60px; } }

