/*------------------------------------*\
:    Header - layout
\*------------------------------------*/
.c-site-header__inner {
  position: relative;
  max-width: 1920px;
  margin: 0 auto;
  height: 100%; }

body.no-scroll {
  height: 100%;
  width: 100%;
  overflow: hidden; }
  body.no-scroll .c-site-header.scroll-header {
    transform: translateY(0); }

/*------------------------------------*\
:    Mobile menu background fade
\*------------------------------------*/
.c-fade-filter {
  position: fixed;
  top: -100px;
  left: -100px;
  right: -100px;
  bottom: -100px;
  background-color: #2d72a9;
  z-index: -1;
  opacity: 0;
  transition-delay: 0.5s;
  pointer-events: none; }
  .c-fade-filter.is-active {
    z-index: 90;
    opacity: 0.9;
    transition: all 0.3s ease-in-out;
    pointer-events: auto; }
  .c-fade-filter.is-white {
    z-index: 1;
    background-color: #ffffff;
    transition-delay: none;
    transition: all 0s 0s ease-in-out; }
